@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#fff}::-webkit-scrollbar-thumb{background:#cfcfcf}body{overflow-x:hidden;color:#333 !important;margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.leaflet-container{min-height:400px;position:relative;top:-80px;z-index:2;opacity:1}body.dark-mode .leaflet-container{opacity:0.85}body.dark-mode .section-title{color:#f5f5f5}body.light-mode{background-color:#fff;color:#333;transition:background-color 0.3s ease}body.dark-mode{background-color:#182130;color:#f5f5f5}body.dark-mode .main-input{background-color:#182130;color:#f5f5f5}body.dark-mode .app-theme-switcher{background-color:#1e2d40;color:#f5f5f5}body.dark-mode .app-logo{background-color:#1e2d40;color:#f5f5f5}body.dark-mode .header-overlay{background:linear-gradient(-45deg, #514A9D, #24C6DC)}span.chakra-switch__track:focus{box-shadow:0 0 0 3px rgba(66,201,225,0.6) !important}body.dark-mode .chakra-stat__label{font-family:'Montserrat';color:#f5f5f5}body.dark-mode .general-content{background-color:#1e2d40}body.dark-mode .list-item-descrip{color:#f5f5f5}body.dark-mode .content-flag{background-color:#182130}@media only screen and (max-width: 1200px){.chakra-stat{margin-bottom:1em}}.chakra-stat{font-family:'Montserrat';display:flex !important;justify-content:center !important}.chakra-stat__number{font-family:'Montserrat';color:#24C6DC}body.dark-mode .main-button{border:none;background:linear-gradient(-45deg, #514A9D, #24C6DC);color:#f5f5f5}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.app-theme-switcher{display:flex;align-items:center;justify-content:space-between;background-color:#fff;box-shadow:0 10px 50px rgba(17,17,17,0.35);padding:10px 25px;border-radius:0.375rem;z-index:10;position:absolute;top:2em;right:2em}.app-theme-switcher .theme-switch-text{opacity:0.8;font-weight:500;margin:0;position:relative;top:-1px}.app-theme-switcher .theme-icon{margin-right:10px}.app-theme-switcher .app-switch{margin-left:1em}@media only screen and (max-width: 500px){.app-theme-switcher{font-size:12px;top:1em;right:1em}}

.app-header-small{height:110px !important;transition:.3s ease !important}.app-header{position:fixed;height:280px;top:0;width:100vw;box-shadow:0 0 50px rgba(17,17,17,0.35);z-index:3;transition:.3s ease !important}.app-header .header-bg{position:absolute;-webkit-filter:grayscale(100%);filter:grayscale(100%);z-index:0;width:100%;height:100%;background-image:url("https://cdn.dribbble.com/users/213309/screenshots/14137936/media/cfdc86de7e47f0fa2bd41233a72c906d.png?compress=1&resize=1000x750")}.app-header .header-overlay{opacity:0.95;position:absolute;z-index:1;width:100%;height:100%;background:#24C6DC;background:linear-gradient(45deg, #514A9D, #24C6DC)}.app-header .app-logo{display:flex;align-items:center;justify-content:space-between;background-color:#fff;box-shadow:0 10px 50px rgba(17,17,17,0.35);padding:10px 25px;border-radius:0.375rem;position:absolute;z-index:8;top:2em;height:50px;font-weight:500;left:2em}.app-header .app-logo .logo-icon{margin-right:10px}.app-header .header-title{font-family:'Montserrat';position:absolute;z-index:4;font-weight:700;letter-spacing:-1px;left:50%;top:55%;transform:translate(-50%, -50%);font-size:40px;color:#f5f5f5;text-align:center}@media only screen and (max-width: 1200px){.header-title{width:80%;font-size:30px !important}}@media only screen and (max-width: 500px){.header-title{font-size:22px !important}.app-logo{font-size:12px;z-index:99;left:1em !important;top:1em !important}}

.input-box-small{top:70px !important;transition:.3s ease}.app-inputbox{position:fixed;top:240px;left:50%;transform:translatex(-50%);z-index:3;width:35vw;transition:.3s ease}.app-inputbox .main-input{font-family:'Montserrat';position:relative;z-index:3;height:80px;border:2px solid transparent;background-color:#fff;padding-left:1.5em;font-size:20px;font-weight:500;color:#333}.app-inputbox .main-input:hover{border:2px solid transparent}.app-inputbox .main-input:focus{border:2px solid transparent;box-shadow:0 0 0 2px rgba(109,107,228,0.2)}.app-inputbox .dummy-input{position:absolute;left:0;z-index:0;box-shadow:0 10px 50px rgba(17,17,17,0.35)}.app-inputbox:hover .dummy-input{box-shadow:0 10px 25px rgba(17,17,17,0.35) !important}.app-inputbox .main-button{font-family:'Montserrat';font-weight:500;border:none;background:linear-gradient(-45deg, #514A9D, #24C6DC);color:#f5f5f5;width:100px;right:1.5em;height:40px;z-index:4;position:absolute;top:50%;transform:translatey(-50%)}.app-inputbox .main-button:hover{background:linear-gradient(-45deg, #514A9D, #24C6DC);opacity:0.8;transition:.3s ease}.app-inputbox .main-button:focus{border:none;box-shadow:0 0 0 3px rgba(109,107,228,0.5)}@media only screen and (max-width: 500px){.app-inputbox{width:95vw}.input-box-small{top:90px !important;transition:.3s ease}}@media only screen and (max-width: 1200px){.app-inputbox{width:80vw}.input-box-small{top:90px !important;transition:.3s ease}}@media only screen and (max-width: 500px){.app-inputbox{width:95vw}.app-inputbox .main-input{font-size:14px}.input-box-small{top:90px !important;transition:.3s ease}}

.content-wrapper{margin-top:360px}.placeholder-map{margin-top:360px;height:400px;width:100vw;background-image:url(/iptracktool/static/media/map.e01038a7.png);background-repeat:no-repeat;background-size:cover;opacity:0.2;z-index:2;position:relative;top:-80px}.placeholder-map .map-overlay{position:absolute;width:100%;height:100%;opacity:0.5;top:0;left:0;background:linear-gradient(45deg, #514A9D, #24C6DC)}.general-content{position:relative;top:-80px;box-shadow:0 0 50px rgba(17,17,17,0.12);border-radius:0.375rem;padding:40px 20px;justify-content:space-between;display:flex;width:60%;margin:0 auto}.general-content .details-list{display:flex;width:100%;flex-direction:column}.general-content .details-list .details-list-item{display:flex;justify-content:space-between;width:100%;border-bottom:1px solid #24C6DC;padding-bottom:10px;margin-bottom:15px}.general-content .details-list .details-list-item .list-item-title{color:#24C6DC;font-family:'Montserrat';margin-right:2em;font-weight:700}.general-content .details-list .details-list-item .list-item-descrip{font-family:'Montserrat'}.content-section-title{width:60%;margin:0 auto;position:relative;top:-40px;color:#979797;z-index:2}.content-section-title .section-title{font-size:50px;font-family:'Montserrat';font-weight:700}.content-flag{width:250px;position:absolute;justify-content:space-between;height:80px;background-color:#fff;box-shadow:0 0 50px rgba(17,17,17,0.35);border-radius:0.375rem;display:flex;align-items:center;padding:0 2em;bottom:2em;left:50%;transform:translatex(-50%);z-index:999}.content-flag .flag-img{border-radius:10px}.content-flag .street-link{display:flex;align-items:center;font-family:'Montserrat';font-weight:600;font-size:14px;color:#fff;background:linear-gradient(-45deg, #514A9D, #24C6DC);border-radius:.375rem;padding:7px 15px}.content-flag .street-link i{font-size:15px;margin-right:5px}@media only screen and (max-width: 1200px){.content-section-title{width:80%}.general-content{width:80%;flex-direction:column;justify-content:flex-start;align-items:flex-start}}@media only screen and (max-width: 500px){.content-section-title{width:95%}.general-content{width:95%}}

